.form {
    width: 400px;
    padding: 10px;
    position: relative;
    margin-top: 400px ;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgb(177, 207, 168);
    box-shadow: 0 -19px 28px rgba(219, 12, 12, 0.973), 0 10px 10px rgba(211, 159, 159, 0.22);
    text-align: center;
    
    
   
    
}



.login-form {
    width: 400px;
    padding: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgb(11, 31, 87);
    box-shadow: 0 -25px 28px rgba(6, 247, 147, 0.973), 0 10px 10px rgba(209, 11, 11, 0.856);
    text-align: center;
    margin: 0;
    background-size: cover;
    border-radius: 25px;
}



